.interval {
    margin: 0 10px;
}

.desc {
    z-index: 1;
    text-align: center;
    visibility: visible;
}

.desc.hide {
    visibility: hidden;
}

.shroomVideo {
    position: absolute;
    z-index: 0;
    top: 0px;
    left: 0px;
    width: 100%;
    height: 100%;
    object-fit: cover;
}

.stockLeft {
    color: black;
    text-align: center;
    width: fit-content;
    margin: 0 auto;
    position: absolute;
    top: 40px;
    right: 75px;
    margin-left: auto;
    margin-right: auto;
    z-index: 100000;
    cursor: pointer;
    background-color: white;
    border-radius: 13px;
    padding: 5px 15px;
}

.nft-jacker.jacked {
    padding: unset;

    .nft-link {
        width: 500px;
        background-color: unset;
        padding-top: unset;
        padding-left: unset;
    }
    .stockLeft {
        color: black;
        text-align: center;
        width: fit-content;
        margin: 0 auto;
        position: absolute;
