.has-chart {
    h1 {
        display: none;
    }
}

.unsold-cart {
    width: 20px;
}

.nft-jacker.jacked {
    ::-webkit-scrollbar-thumb {
        background: white !important;
    }
    .music-chart {
        -ms-overflow-style: unset;
        scrollbar-width: unset;
        background-color: black;
        border-top-right-radius: 30px;
        border-top: 2px solid white;
        padding-top: 140px;
        border-right: 2px solid white;
        .chart-item {
            .song-info-container {
                .cover-image {
                    border-radius: 0px;
                }
            }
        }
 
    }
}

.tab-container {
    display: flex;
    z-index: 1;
    position: absolute;
    left: 20px;
    top: 90px;
    border-radius: 15px;
    font-size: 11pt;
    border-color: white;
    border-style: solid;
    border-width: 2px;
    overflow: hidden;
    z-index: 100000;

    .tab {
        padding: 10px 15px;
        padding-top: 3px;
        padding-bottom: 3px;
        width: fit-content;
        height: fit-content;
        color: black;
        text-align: center;
        cursor: pointer;
    }

    .tab.not-selected {
        background-color: black;
        color: darkgray;
    }

    .tab.selected {
        background-color: white;
    }
}

.nft-jacker.jacked {
    .tab-container {
        z-index: 1000000;
        font-size: 13px;

        .tab {
            padding: 10px;
        }
    }

    .music-chart {
        width: 270px;
        z-index: 1000000;
        padding-top: unset;
        height: 80%;
        top: 150px;

        .chart-item {
            height: 170px;
            width: 100px;

            .song-info-container {
                .cover-image {
                    width: 100px;
                    height: 100px;
                }
                .song-info {
                    .song {
                        font-size: 12px;
                        overflow-wrap: anywhere;
                    }
                    .artist {
                        font-size: 12px;
                    }
                }
            }
        }
    }
}

.music-chart {
    overflow: scroll;
    height: 100%;
    overflow-x: hidden;
    display: flex;
    flex-wrap: wrap;
    justify-content: left;
    -ms-overflow-style: none;
    scrollbar-width: none;
    width: 100%;
    position: absolute;
    left: 0;
    bottom: 0;
    padding: 0 10px;
    padding-top: 140px;

    .progress-bar {
        margin: auto;
    }

    .song-info-container {
        float: left;
        display: flex;
        flex-direction: column;
    }

    .song-info {
        display: inline-block;
        margin-top: 5px;

        .sold {
            color: red;
            font-size: 8pt;
        }
        a {
            color: darkgray;
        }

        a:hover {
            text-decoration: underline;
        }
        
        .artist {
            font-size: 15px;
        }

        .song {
            font-weight: 700;
            display: flex;
            justify-content: space-between;
        }
    }

    .chart-item {
        margin: 10px;
        display: flex;
        flex-direction: column;
        justify-content: space-between;
        align-items: center;
        align-content: center;
        height: 290px;
        width: 200px;
    }

    .from-text {
        float: left;
        font-size: 12px;
        color: darkgray;
    }
    
    .cover-image{
        width: 200px;
        height: 200px;
        border-radius: 10px;
        // border-style: solid;
        // border-width: 1px;
        // border-color: white;
    }
}

.music-chart::-webkit-scrollbar {
    display: none;
}

@media screen and (max-width: 600px) {

    .nft-jacker.jacked {
        .tab-container {
            z-index: 1000000;
            top: 80px;
            font-size: 8pt;
            right: 15px;
            width: 295px;
    
            .tab {
                width: 25vw;
                padding: 10px;
                padding-top: 7px;
                padding-bottom: 7px;
            }
        }
    
        .music-chart {
            top: 130px;
            justify-content: center;
            width: 100%;
            background-color: #000000e8;
            backdrop-filter: blur(4px);
            border-radius: 0px;
        }
    }


    .tab-container {
        z-index: 10000000;
        top: 65px;
        left: 15vw;
        font-size: 8pt;
        margin-left: 0px;
        .tab {
            width: 23vw;
            padding: 10px;
            padding-top: 7px;
            padding-bottom: 7px;
        }
    }

    .music-chart {
        height: 100%;
        top: 0px;
        justify-content: center;
        background-color: black;
        width: 100%;
        padding-top: 130px;
        position: fixed;
        padding-bottom: 80px;

        .chart-item {
            width: 40vw;
            height: 50vw;

            .song-info-container {
                .cover-image {
                    width: 34vw;
                    height: 34vw;
                    border-radius: 0px;
                }
                .song-info {
                    .song {
                        font-size: 12px;
                        overflow-wrap: anywhere;
                    }
                    .artist {
                        font-size: 12px;
                    }
                }
            }
        }
    }
}
