.player-menu {
    position: relative;
    cursor: pointer;
    width: 80px;
    margin-left: 10px;
    border-bottom: 1px solid white;
    text-decoration: none !important;
    font-size: 16pt;

    .player-menu-items {
        border: 1px solid white;
        padding-top: 8px;
        position: absolute;
        top: 35px;
        background-color: #34343466;
        backdrop-filter: blur(8px);
        color: white;
        max-height: 80vh;
        overflow-y: scroll;
        border-bottom-left-radius: 8px;
        border-bottom-right-radius: 8px;

        .go-to-player.selected {
            background-color: white;
            color: black;
        }
        
        .go-to-player {
            display: flex;
            position: relative;
            img.for-sale {
                display: unset !important;
                width: 15px;
                position: absolute;
                top: 13px;
                right: 65px;
            }
            img.menu-thumb {
                display: unset !important;
                margin-left: auto;
                margin-right: 10px;
                width: 30px;
                height: 30px;
                border-radius: 3px;
            }
            padding: 4px;
            width: 190px;
            padding-left: 18px;
            line-height: 35px;
            font-size: 12pt;
        }
    }

    .arrow-down, .arrow-up {
        position: absolute;
        top: 10px;
        right: 0px;
    }
    .arrow-down {
        width: 0; 
        height: 0; 
        border-left: 7px solid transparent;
        border-right: 7px solid transparent;
        
        border-top: 9px solid white;
    }
    .arrow-up {
        width: 0; 
        height: 0; 
        border-left: 6px solid transparent;
        border-right: 6px solid transparent;
        
        border-bottom : 8px solid white;
    }
}

.player-menu:hover {
    background-color: #ffffff1c;
    animation-name: brighten-menu;
    animation-duration: 0.8s;
}

@keyframes brighten-menu {
    from {
        background-color: transparent;
    }
    to {
        background-color: #ffffff1c;
    }
}
